<template>
<div class="content-box">
        <Header class="header">
            
            <router-link tag="p" slot="first" to='/Cityone' class="el-icon-search"></router-link>
          
            <router-link to="/watchcomputed" tag="p" slot="mid">watch/computed</router-link>
            <router-link slot="last" v-if="this.$store.state.log==''" tag="div" to='/logon'>登录/注册</router-link>
            <router-link slot="last" v-if="this.$store.state.log!=''" tag="div" to='/message'><p class="el-icon-user"></p></router-link>
        </Header>

    <!-- 身体 -->
    
    <div class="content-one">
        <mescroll-vue ref="mescroll" :down="mescrollDown" :up="mescrollUp" @init="mescrollInit">
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide">
                        <ul >
                            <li v-for="(item,index) in list" :key="index">
                                <img :src="`https://fuss10.elemecdn.com${item.image_url}`" alt="">
                                <p>{{item.title}}</p>
                            </li>
                        </ul>
                    </div>
                    <div class="swiper-slide">
                        <ul >
                            <li v-for="(i,index) in list2" :key="index">
                                <img :src="`https://fuss10.elemecdn.com${i.image_url}`" alt="">
                                <p>{{i.title}}</p>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="swiper-pagination"></div>
            </div>
            <!-- 自己做的加载中圈圈 -->
            <!-- <div class="upload" v-if="Show"><img src="../assets/upload.gif" alt=""></div> -->

            <!-- 上拉下载的数据 -->
            <div @click="Skin(item.id)" v-for="(item,index) in dataList" :key="index" class="content-div">
                <div><img :src="'//elm.cangdu.org/img/'+item.image_path" alt=""></div>
                <ul class="content-ul">
                    <li>
                        <p class="cont-p-one">
                            <b>品牌<span>{{item.category}}</span></b>  
                            <ul data-v-2df9c5b6="" class="shop_detail_ul">
                                <li data-v-2df9c5b6="" class="supports">保</li>
                                <li data-v-2df9c5b6="" class="supports">准</li>
                                <li data-v-2df9c5b6="" class="supports">票</li>
                            </ul>
                        </p>
                        <p class="cont-p-tow">
                            <section class="cont-p-tow-sec">
                                <el-rate
                                    v-model="item.rating" :style="{width:'1.6rem'}"
                                    show-score
                                    text-color="#ff9900"
                                    score-template="{value}">
                                    </el-rate>
                                <p>月送{{item.recent_order_num}}</p>
                            </section>
                            <section data-v-2df9c5b6="" class="rating_order_num_right">
                                <span class="delivery_left">蜂鸟专送</span> 
                                <span class="delivery_right">准时达</span>
                            </section> 
                            
                        </p>
                        <p class="cont-p-three">
                            <span>￥{{item.float_minimum_order_amount}}/配送费约￥{{item.float_delivery_fee}}</span>
                            <span>{{item.distance}}/{{item.order_lead_time}}</span>
                        </p>
                        
                    </li>
                </ul>
            </div>
        </mescroll-vue>
    </div>
</div>
</template>

<script>
import Footers from './footers.vue'
import Header from './header.vue'

import MescrollVue from 'mescroll.js/mescroll.vue'
export default {
    name: "content.vue",
    components:{
        Header,
         MescrollVue // 上拉下载
    },
    data(){
        return {
            list:[],
            list2:[],
            arr:[],
            value:"",
            Show:false,

             mescroll: null, // mescroll实例对象
			      mescrollDown:{}, //下拉刷新的配置. (如果下拉刷新和上拉加载处理的逻辑是一样的,则mescrollDown可不用写了)
			      mescrollUp: { // 上拉加载的配置.
			        callback: this.upCallback, // 上拉回调,此处简写; 相当于 callback: function(page, mescroll) { }
			        //以下是一些常用的配置,当然不写也可以的.
                    page: {
                        num: 0, //当前页 默认0,回调之前会加1; 即callback(page)会从1开始
                        size: 20 //每页数据条数,默认10
                    },
                    htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
                    noMoreSize: 5, //如果列表已无数据,可设置列表的总数量要大于5才显示无更多数据;
                                //避免列表数据过少(比如只有一条数据),显示无更多数据会不好看
                                //这就是为什么无更多数据有时候不显示的原因
                    toTop: {
                        //回到顶部按钮
                        src: "./static/mescroll/mescroll-totop.png", //图片路径,默认null,支持网络图
                        offset: 1000 //列表滚动1000px才显示回到顶部按钮
                    },
                    empty: {
                        //列表第一页无任何数据时,显示的空提示布局; 需配置warpId才显示
                        warpId: "xxid", //父布局的id (1.3.5版本支持传入dom元素)
                        icon: "./static/mescroll/mescroll-empty.png", //图标,默认null,支持网络图
                        tip: "暂无相关数据~" //提示
                    }
			    },
			      dataList: [] // 列表数据
        }
    },
    //上拉下载
    beforeRouteEnter (to, from, next) { // 如果没有配置回到顶部按钮或isBounce,则beforeRouteEnter不用写
			    next(vm => {
			       // 找到当前mescroll的ref,调用子组件mescroll-vue的beforeRouteEnter方法
			      vm.$refs.mescroll && vm.$refs.mescroll.beforeRouteEnter() // 进入路由时,滚动到原来的列表位置,恢复回到顶部按钮和isBounce的配置
			    })
			  },
			  beforeRouteLeave (to, from, next) { // 如果没有配置回到顶部按钮或isBounce,则beforeRouteLeave不用写
			     // 找到当前mescroll的ref,调用子组件mescroll-vue的beforeRouteLeave方法
			    this.$refs.mescroll && this.$refs.mescroll.beforeRouteLeave() // 退出路由时,记录列表滚动的位置,隐藏回到顶部按钮和isBounce的配置
			    next()
			  },
    methods:{
        async Carousel(){
            this.Show=true;      
           
            await this.axios.get('http://elm.cangdu.org/v2/index_entry?geohash=39.78493,116.49476&group_type=1&flags[]=F').then((res)=>{
                // console.log(res)
                this.list=res.data
                //第二页轮播
                var a=res.data.splice(8,15)
                this.list2=a
                // console.log(this.list2)
            })
        },

        //为了把详情页面图片存进vuex中
       async Skin(id){
            this.$router.push({
                path:'/shopDetails',
                name:'shopDetails',
                params:{
                    id:id,
                }
            })
            //console.log(id)        
       },
        crea(){
            // console.log(this.$route.params.geohash)
        },
            
         // mescroll组件初始化的回调,可获取到mescroll对象
			    mescrollInit (mescroll) {
			      this.mescroll = mescroll  // 如果this.mescroll对象没有使用到,则mescrollInit可以不用配置
			    },
			    // 上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
			    upCallback (page, mescroll) {
			      // 联网请求
			      this.axios.get(`http://elm.cangdu.org/shopping/restaurants?latitude=31.22299&longitude=121.36025&offset=${this.mescrollUp.page.num}&limit=${this.mescrollUp.page.size}&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=`, {
			        params: {
			          num: page.num, // 页码
			          size: page.size // 每页长度
			        }
			      }).then((response) => {
                      			        // 请求的列表数据
			        let arr = response.data
			        // 如果是第一页需手动置空列表
			        if (page.num === 1) this.dataList = []
			        // 把请求到的数据添加到列表
                    this.dataList = this.dataList.concat(arr)
                    
                    //把图片存进vuex
                    this.$store.commit('skin',this.dataList)
			        // 数据渲染成功后,隐藏下拉刷新的状态
			        this.$nextTick(() => {
			          mescroll.endSuccess(arr.length)
			        })
			      }).catch((e) => {
			        // 联网失败的回调,隐藏下拉刷新和上拉加载的状态;
			        mescroll.endErr()
			      })
			    }

    },
    updated(){
        new Swiper(".swiper-container",{
            autoplay:2000,
            loop:true,
            pagination:".swiper-pagination"
        })
    },
    
    mounted(){
        //自己做的加载圈
        /*this.Carousel()*/
    },
}
</script>

<style lang='scss'>
.content-box{
    width: 100%;height: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 0.4rem;
    .content-one{
       overflow: auto;
       flex: 1;
       .swiper-container{
           width: 100%;
           height: 3.34rem;
           .swiper-slide{
               width: 100%;
               height: 100%;
               ul{
                margin: 0;
                padding: 0;
                height: 3.34rem;
                display: flex;
                flex-wrap: wrap;
                    li{
                        margin: 0;padding: 0;
                        list-style: none;
                        width: 25%;height: 50%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        // overflow: hidden;
                        img{
                            height: 40%;width: 42%;
                        }           
                    }
                }
           }
       }
       
    }
}
.upload{
    position: fixed;
    left:40%;top:50%;
}
.content-div{
    width: 100%;height: 1.74rem;
    border-bottom: 0.01rem solid #E5E5E5;
    display: flex;
    align-items: center;
    // justify-content:space-around;
    .content-ul{
        width: 72%;height: 1.16rem;
        li{
            .cont-p-one{
                font-size: 0.26rem;margin-top:0.04rem; 
                display: flex;justify-content: space-between;
                b{
                    background: #F8D943;
                    font-size: 0.2rem;
                    span{background:#fff;font-size: 0.24rem;}
                }
            }
            .cont-p-tow{
                 display: flex;width: 100%;
                font-size: 0.18rem;margin-top:0.18rem; 
                justify-content: space-between;
                .cont-p-tow-sec{display: flex}
                .rating_order_num_right{
                    font-size: 0.12rem;
                    .delivery_left{
                        border: 0.008rem solid #3190e8;
                        color: #3190e8;border-radius: 0.04rem;
                    }
                    .delivery_right{
                        background: #3190e8;
                         color: #fff;border-radius: 0.04rem;
                    }
                }
                p{color: #666;}
            }
            .cont-p-three{
                font-size: 0.18rem;width: 100%;
                display:flex;justify-content: space-between;
                word-spacing: 0.8rem;
                margin-top:0.12rem; 
                color: #666;
            }
        }
    }
    div{
        width: 1.34rem;
        text-align: center;
        img{
            width:1.05rem;height: 1.05;
            line-height: 1.74rem;
        }
    }
    
}

.shop_detail_ul[data-v-2df9c5b6] {
    display: -ms-flexbox;
    display: flex;
    transform: scale(.8);
    margin-right: -.15rem;
    // float: right;
}
.supports[data-v-2df9c5b6] {
    font-size: .2rem;
    color: #999;
    border: .025rem solid #f1f1f1;
    padding: 0 .04rem;
    border-radius: .08rem;
    margin-left: .05rem;
}
p{
    margin: 0; padding: 0;
} 
ul{
   margin: 0; padding: 0; 
   li{
    margin: 0; padding: 0; 
    list-style: none;
   }
}
a{
    color: #fff;
}     

</style>


